import { firebaseapp } from "../init"
const answers=["a","b","c","d"]
export const uploadQuestion=async(text,file,answer,db)=>{
    try{
        let str=""
        let imagepath=""
        if(!answers.includes(answer.toLowerCase())){
            return{
                status:400,
                message:"Answer should be in between a,b,c,d"
            }
        }
        if(file){
            let resp=await firebaseapp.storage().ref(db+"/"+file.name).put(file)
            str=await resp.ref.getDownloadURL()
            imagepath=`${db}/${file.name}`

        }
        await firebaseapp.firestore().collection(db).add({
            text,
            imageurl:str,
            answer:answer.toLowerCase(),
            imagepath
        })
        return {
            status:200,
            message:"Successfully done"
        }
    }catch(err){
        return {
            status:400,
            message:err.message
        }
    }
} 
export const fetchAllQuestions=async(db,setData)=>{
    try{
        firebaseapp.firestore().collection(db).onSnapshot(snap=>{
            if(snap.empty){
                setData([])
            }else{
                let temp=[]
                let docs=snap.docs
                for(let i=0;i<docs.length;i++){
                    temp.push({id:docs[i].id,...docs[i].data()})
                }
                setData(temp)
            }
        },(err)=>{

        })
    }catch(err){

    }
}

export const deleteSingleQuestion=async(id,db)=>{
    try{
        await firebaseapp.firestore().collection(db).doc(id).delete()
        return{
            status:200,
            message:"Success"
        }
    }catch(err){
        return{
            status:400,
            message:err.message
        }
    }
}