import React,{useEffect,useContext} from 'react'
import './style.css';
import { fetchAuthStatus } from '../../firebase/auth';
import { AuthContext } from '../../context/auth';
import AdminTemplate from '../../components/admin'
import UserTemplate from '../../components/user'
import Header from '../../components/header'
const Home = () => {
	let [auth,setAuth]=useContext(AuthContext)
	useEffect(()=>{
		fetchAuth()
	},[])
	const fetchAuth=async()=>{
		let res=await fetchAuthStatus()
		setAuth(res)
	}
	return(
		<div>
			<Header/>
			{auth.isAdmin&&<AdminTemplate/>}
			{<UserTemplate/>}
		</div>
	);
};

export default Home;
