import React from 'react';
import './App.css';
import {BrowserRouter as Router,Route,Switch} from 'react-router-dom'
import Home from './routes/home';
import AllQuestions from './routes/Questions'
import AuthProvider from './context/auth';
import PracticeExam from './components/user/exam';

function App() {
  return (
    <AuthProvider>
    <Router>
      <Switch>
        <Route path="/practice/" component={Home} exact/>
        <Route path="/practice/allquestions" component={AllQuestions} exact/>
        <Route path="/practice/exam/:db" component={PracticeExam}/>
      </Switch>
    </Router>
    </AuthProvider>
  );
}

export default App;
