import React,{useState} from 'react'
import './style.css'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles';
import { uploadQuestion } from '../../firebase/admin';
import AuthModal from './authModal'
const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      height: 140,
      width: 100,
    },
    control: {
      padding: theme.spacing(2),
    },
  }));
export default function AdminTemplate(){
    const classes = useStyles();
    const [text11,setText11]=useState("")
    const [text12,setText12]=useState("")
    const [text11and12,setText11and12]=useState("")
    const [file11,setFile11]=useState("")
    const [file12,setFile12]=useState("")
    const [file11and12,setFile11and12]=useState("")
    const [answer11,setAnswer11]=useState("")
    const [answer12,setAnswer12]=useState("")
    const [answer11and12,setAnswer11and12]=useState("")
    const [open,setOpen]=useState(true)
    
    const handle11Submit=async()=>{
        let res=await uploadQuestion(text11,file11,answer11,"joint11practice")
        if(res.status===200){
            setText11("")
            setFile11("")
            setAnswer11("")
        }else{
            alert(res.message)
        }
    }
    const handle12Submit=async()=>{
        let res=await uploadQuestion(text12,file12,answer12,"joint12practice")
        if(res.status===200){
            setText12("")
            setFile12("")
            setAnswer12("")
        }else{
            alert(res.message)
        }
    }
    const handle11and12Submit=async()=>{
        let res=await uploadQuestion(text11and12,file11and12,answer11and12,"joint11and12practice")
        if(res.status===200){
            setText11and12("")
            setFile11and12("")
            setAnswer11and12("")
        }else{
            alert(res.message)
        }
    }

    return(
        <div>
            <Grid container className={classes.root} spacing={2}>
                <Grid item xs={12}>
                    <Grid container justify="center" spacing={2}>
                    <Grid item>
                    <Paper elevation={3} className="card1admin">
                        <div style={{paddingLeft:20,paddingRight:20}}>
                        <p className="cardHeadingadmin">11 joint question upload</p>
                        <textarea className="inputBoxadmin" placeholder="Question text"
                            value={text11}
                            onChange={(e)=>{
                                setText11(e.target.value)
                            }}
                        />
                        <br/>
                        <label className="labelInputadmin"> Enter Your File
                        <input type="file" size="60" className="inputFileadmin"
                            onChange={(e)=>{
                                setFile11(e.target.files[0])
                            }}
                        />
                        </label>
                        <p style={{color:"white"}}>{file11.name}</p> 
                        <br/>
                        <textarea className="inputBoxadmin" placeholder="Answer here"
                            value={answer11}
                            onChange={(e)=>{
                                setAnswer11(e.target.value)
                            }}
                        />
                        <br/>
                        <Button variant="contained" onClick={handle11Submit}
                        style={{backgroundColor:"white",color:"#c71d6f",marginTop:10}}>
                        Upload</Button>
                        </div>
                    </Paper>
                    </Grid>

                    <Grid item>
                    <Paper elevation={3} className="card2admin">
                        <div style={{paddingLeft:20,paddingRight:20}}>
                        <p className="cardHeadingadmin">11 joint question upload</p>
                        <textarea className="inputBoxadmin" placeholder="Question text"
                             value={text12}
                            onChange={(e)=>{
                                setText12(e.target.value)
                            }}
                        />
                        <br/>
                        <label className="labelInputadmin"> Enter Your File
                        <input type="file" size="60" className="inputFileadmin" 
                             onChange={(e)=>{
                                setFile12(e.target.files[0])
                            }}
                        />
                        </label> 
                        <p style={{color:"white"}}>{file12.name}</p>
                        <br/>
                        <textarea className="inputBoxadmin" placeholder="Answer here"
                             value={answer12}
                            onChange={(e)=>{
                                setAnswer12(e.target.value)
                            }}
                        />
                        <br/>
                        <Button variant="contained" onClick={handle12Submit}
                        style={{backgroundColor:"white",color:"#7a21a6",marginTop:10}}>
                        Upload</Button>
                        </div>
                    </Paper>
                    </Grid>

                    <Grid item>
                    <Paper elevation={3} className="card3admin">
                        <div style={{paddingLeft:20,paddingRight:20}}>
                        <p className="cardHeadingadmin">11 joint question upload</p>
                        <textarea className="inputBoxadmin" placeholder="Question text"
                             value={text11and12}
                            onChange={(e)=>{
                                setText11and12(e.target.value)
                            }}
                        />
                        <br/>
                        <label className="labelInputadmin"> Enter Your File
                        <input type="file" size="60" className="inputFileadmin"
                            onChange={(e)=>{
                                setFile11and12(e.target.files[0])
                            }}
                        />
                        </label> 
                        <p style={{color:"white"}}>{file11and12.name}</p>
                        <br/>
                        <textarea className="inputBoxadmin" placeholder="Answer here"
                            value={answer11and12}
                            onChange={(e)=>{
                                setAnswer11and12(e.target.value)
                            }}
                        />
                        <br/>
                        <Button variant="contained" onClick={handle11and12Submit}
                        style={{backgroundColor:"white",color:"#240b36",marginTop:10}}>
                        Upload</Button>
                        </div>
                    </Paper>
                    </Grid>
                </Grid>
                </Grid>
            </Grid>
        </div>
    );
}