import Axios from 'axios'

import {firebaseapp} from '../init'
import jsCookie from 'js-cookie'
import { url } from '../../url'
export const fetchAuthStatus=async()=>{
    try{
        let jwt=jsCookie.get('userjwt')
        let res=await Axios.get(url+"/auth/checkJWT/"+jwt)
        if(res.data.message==="Valid"){
            if(res.data.admin){
                return {
                    isAdmin:true,
                    isLoggedIn:true
                }
            }else{
                return{
                    isAdmin:false,
                    isLoggedIn:true
                }
            }
        }else{
            return{
                isAdmin:false,
                isLoggedIn:false
            }
        }
    }catch(err){
        return{
            isAdmin:false,
            isLoggedIn:false
        }
    }
}